/*
 * react native的transform属性对应一个对象数组
 * http://facebook.github.io/react-native/docs/transforms.html#proptypes
 *   [
 *      {scaleX: 2},
 *      {scaleY: 2}
 *   ] => scaleX(2) scaleY(2)
 */

import cssName from './cssName'
import fixUnit from './fixUnit'


function fixTransform(style, result) {

    let name = 'transform';
    let convertName = ['transform','transformMatrix'];
    let transformations = [];
    // fixed transform:rn数组形式的fixed成普通的web格式
    if (style[convertName[0]]){
        let value = style[convertName[0]];
        if (Array.isArray(value)) {
            value.forEach(function (transformation) {
                let key = Object.keys(transformation)[0]
                let val = transformation[key]
                if(!val){val=0}
                // for animated value

                if (val.__getValue) {
                  val = val.__getValue()
                }
                val = fixUnit(val, key)
                transformations.push(key + '(' + val + ')')
            });
        } else if (typeof value === 'string') {
            transformations.push(value)
        }
    }

    // fixed transformMatrix
    if(style[convertName[1]]){
        let value = style[convertName[1]];
        transformations.push('matrix3d' + '(' + value.join(',') + ')')
    }

    name = cssName(name) || name;
    result[name] = transformations.join(' ');

}
let fixTransformObj={
    names:  {
        transform: true,
        transformMatrix: true,
    },
    handle: fixTransform
}
export default fixTransformObj

